$light: whitesmoke;
// $dark: #131b5485;
$dark: #7b83bd56;
$header: #4c5285af;

@mixin font($color) {
    color: $color;
}

@mixin background($tone, $radius: 10px, $blur: 10px) {
    background-color: $tone;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur($blur);
    border-radius: $radius;
}
