@include fonts;
// @include arial;

* {
    // -ms-overflow-style: none;
    scrollbar-width: none;
    // box-sizing: border-box;
}

// ::-webkit-scrollbar {
//     display: none;
// }

@function factor($z) {
    @return 1 + ($z) / $perspective;
}

$perspective: 10px;
$back: -90px;
$top: 1px;
$text: -1px;

.Main {
    perspective: $perspective;
    // overflow-x: hidden;
    overflow-y: auto;
    width: 100vw;
    // height: 100vw;
    // -ms-overflow-style: none;
    // scrollbar-width: initial;
    // box-sizing: border-box;
}

.Parallax {
    &__top {
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 2;
    }
    &__back {
        background-image: url("../../pictures/backgrounds/RetroParallax.jpg");
        width: 100vw;
        height: 1620px;
        justify-content: center;
        // min-height: 100vh;
        background-repeat: no-repeat;
        // background-size: cover;
        position: absolute;
        z-index: -2;
        // transform: translateZ($back) scale(factor($back));
    }

    &__middle {
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        z-index: 1;
    }
    &__above {
        background-repeat: no-repeat;
        background-size: cover;
        position: absolute;
        z-index: 3;
    }
    &__elements {
        z-index: 20;
        width: 100%;
    }
    &__group {
        position: relative;
        height: 100vh;
        z-index: 2;
        transform-style: preserve-3d;
    }
}

